<template>
  <div id="sidenav">
    <div class="extend" v-if="extend">
      <div class="open_extend" @click="open_extend">
        <i class="el-icon-s-unfold"></i>
      </div>
      <div id="hzau" @click="open_extend">
        <div class="hazu-item">华</div>
        <div class="hazu-item">中</div>
        <div class="hazu-item">农</div>
        <div class="hazu-item">业</div>
        <div class="hazu-item">大</div>
        <div class="hazu-item">学</div>
      </div>
    </div>
    <div class="close_extend" v-else>
      <i class="el-icon-s-fold" @click="close_extend"></i>
      <div class="function_item" @click="Home" style="cursor:pointer">系统首页</div>
      <div class="function_item" @click="UserInformation" style="cursor:pointer">个人中心</div>
      <div class="function_item" style="cursor:pointer">种质资源库</div>
      <div class="setting">
      <i id="setting_btn" class="el-icon-s-tools" @click="open_setting"></i>
      <div class="base_setting" v-show="IsSetting">
        <div id="information" @click="UserInformation" style="cursor:pointer">个人信息</div>
        <div id="information" @click="Changepwd()" style="cursor:pointer">修改密码</div>
        <div id="logout" style="cursor:pointer">退出登录</div>
      </div>
      <!-- <ChangepwdPage @hidden="hiddenShow" v-show="dialogVisible"></ChangepwdPage> -->
      <el-dialog
        title="修改密码"
        :visible.sync="dialogVisible"
        width="500px"
        append-to-body
      >
        <el-form>
          <el-form-item label="旧密码：">
            <el-input
              placeholder="请输入密码"
              v-model="pwdOld"
              show-password
              @input="changeSequence"
            ></el-input>
          </el-form-item>
          <el-form-item label="新密码：">
            <el-input
              type="password"
              auto-complete="off"
              placeholder="请输入新密码"
              v-model="pwdNew1"
              show-password
              @input="changeSequence"
            ></el-input>
          </el-form-item>
          <el-form-item label="确认密码：">
            <el-input
              type="password"
              auto-complete="off"
              placeholder="请再次输入新密码"
              v-model="pwdNew2"
              show-password
              @input="changeSequence"
            ></el-input>
          </el-form-item>
          <el-form-item>
            <el-button type="primary">确认修改</el-button>
          </el-form-item>
        </el-form>
        <span slot="footer" class="dialog-footer">
          <el-button @click="dialogVisible = false">取 消</el-button>
          <el-button type="primary" @click="dialogVisible = false">确 定</el-button>
        </span>
      </el-dialog>
    </div>
  </div>
</div>
</template>
<script>
export default {
  data() {
    return {
      extend: "true", //判断侧边导航栏状态
      IsSetting: false, //判断设置按钮打开与否
      dialogVisible: false, //判断是否弹出修改密码对话框
    };
  },
  methods: {
    //打开侧边导航栏
    open_extend() {
      this.extend = !this.extend;
      document.getElementById("sidenav").style.flex = 7;
    },
    //关闭侧边导航栏
    close_extend() {
      this.extend = !this.extend;
      this.IsSetting = false;
      document.getElementById("sidenav").style.flex = 1;
    },
    open_setting() {
      this.IsSetting = !this.IsSetting;
      console.log(this.IsSetting);
    },
    UserInformation() {
      this.$router.push('/User/UserInformation')
    },
    Changepwd() {
      this.dialogVisible = true;
    },
    Home() {
      this.$router.push('/Home')
    },
    changeSequence() {
      this.$forceUpdate();
    },
  } 
}

</script>
<style>
#sidenav {
  position: relative;
  height: 100%;
  text-align: center;
  background-color: #15395b;
  transition: flex 0.5s;
  flex: 1;
  z-index: 9;
  overflow: hidden;
  color: white;
}
#setting_btn {
  width: 100%;
}
.open_extend:hover,
.el-icon-s-fold:hover,
#setting_btn:hover {
  background-color: #69c0ff;
  cursor: pointer;
}
#hzau {
  width: 100%;
  position: absolute;
  top: 45%;
  transform: translateY(-50%);
}
#hzau:hover {
  cursor: pointer;
}
.function_item:first-of-type {
  margin-top: 20px;
  border-top: 1px #ffffff solid;
}
.function_item {
  word-break: keep-all;
  text-align: center;
  height: 50px;
  line-height: 50px;
  border-bottom: 1px #ffffff solid;
}
.hzau-item {
  text-align: center;
}
.setting {
  position: absolute;
  bottom: 10px;
  width: 100%;
  left: 50%;
  transform: translateX(-50%);
  /* overflow: visible; */
}
.base_setting {
  width: 100%;
  min-width: 150px;
  position: absolute;
  background-color: #0fa432;
  bottom: 0px;
  right: 0px;
  transform: translateY(-50%);
  /* display: none; */
}
.base_setting div {
  height: 40px;
  text-align: center;
  line-height: 40px;
  border: 1px #b3eebc solid;
}
.extend,
.close_extend {
  margin-top: 10px;
}
.el-icon-s-unfold {
  font-size: 20px;
}
.el-icon-s-fold {
  font-size: 20px;
  width: 100%;
}
.el-icon-s-tools {
  font-size: 20px;
}
</style>